<template>
   <div>
      <mt-header class="hc-header"  :title="result.store.shortName">
        <mt-button @click="back" slot="left" icon="back"></mt-button>
        <Star :score="score" slot="right"/>
      </mt-header>
      <div slot="content">
        <div class="hc-main-content">
          <div class="hc-main-item hc-pr hc-clearfix">
              <div class="hc-main-sign hc-pa" v-bind:style ="{backgroundImage:'url('+result.store.storeDescription.imgUrl+')'}">
              </div>
              <div class="hc-main-introduce hc-fl">
                <div class="hc-main-address hc-cb3 hc-f12">
                  <span>
                    <svg class="icon" aria-hidden="true">
                      <use xlink:href="#icon-lujing6"></use>
                    </svg>
                  </span>
                  {{result.store.storeDescription.adress}}</div>
                <div class="hc-main-address hc-cb3 hc-f12">
                  <span>
                    <svg class="icon" aria-hidden="true">
                      <use xlink:href="#icon-ziyuan20"></use>
                    </svg>
                  </span>
                  0588-3454325
                </div>
                <div class="hc-main-label">
                  <div class="hc-main-label-1">
                    <div class="hc-main-label-icon hc-cfff hc-inlineblock">
                      <svg class="icon hc-f14" aria-hidden="true">
                        <use xlink:href="#icon-ziyuan1"></use>
                      </svg>
                    </div>
                    一箱起送
                  </div>
                  <div class="hc-main-label-1 hc-label2-2">
                    支持自取
                  </div>
                </div>

              </div>
            </div>
          </div>
          <div class="hc-nav-content hc-f14">
            <mt-swipe :auto="0" :show-indicators="false">
              <mt-swipe-item>
                <div v-for="(item, index) in result.categoryList" :key="index"
                    :class="{'hc-nav-item':true, 'hc-text-center':true, active:result.categoryId == item.id}"
                    @click="handleItem(item)"
                >
                  <div class="hc-w100-per">
                    <span class="hc-nav-items">{{item.name}}</span>
                  </div>
                </div>
              </mt-swipe-item>
            </mt-swipe>
          </div>
          <div class="hc-store-list">
            <div @click="toGoodsDetails" v-for="(item,index) in result.productList" v-bind:key="index" class="hc-store-item " >
              <div class="hc-flex">
                <div class="hc-flex-item">
                  <img class="thrume" :src="item.mainImage" alt="">
                </div>
                <div class="hc-flex-item hc-flex-item-2">
                  <div class="hc-store-content">
                    <h3>{{item.productName}}</h3>
                    <p>{{item.spec.winePercent}}度{{item.spec.material}} {{item.spec.wineMl}}ml {{item.spec.nums}}瓶</p>
                    <p>{{item.subtitle}}</p>
                    <div class="hc-flex hc-store-price">
                      <div class="hc-flex-item ">
                        ￥ <span class="price">{{item.price}}</span>
                      </div>
                      <div class="hc-flex-item hc-text-right">
                        <hc-cart-btn
                          class="hc-cart-btn"
                          :tempNum="tempNum"
                          @subtractFromCart="subtractFromCart"
                          @addToCart="addToCart"
                        ></hc-cart-btn>
                      </div>
                    </div>
                  </div>
                </div>
            </div>
          </div>
        </div>
      </div>
      <hc-shop-cart
        v-model="popupRoleVisible"
        ref="hcCart"
        :needHide='true'
        :userId='userId'
        :storeId='storeId'
        :productId='productId'
      ></hc-shop-cart>
   </div>
</template>
<script>
import Star from '../../components/common/star/Star';
import HcCartBtn from '../../components/common/hcCartBtn/hcCartBtn'
import hcShopCart from '../../components/common/hcShopCart'
import { productList } from '../../api/axios.js'
export default {
  name:'store',
  data(){
    return {
      score:9,
      result:{},
      popupRoleVisible: false,
      userId: '',
      storeId: '',
      productId: '',
      tempNum: 2
    }
  },
  components:{
     Star,
     HcCartBtn,
     hcShopCart
  },
  methods:{
    handleItem(item){
      this.result.categoryId = item.id;
    },
    getProductList(){
      productList().then((result) => {
        console.info(result)
        this.result = result.result;
        setTimeout(() => {
          this.$refs.hcCart.getCartVo()
        },0)
      })
    },
    subtractFromCart() {

      this.tempNum -= 1;
      this.$refs.hcCart.updataCart({})
      if(this.tempNum <= 0) this.tempNum = 0;
    },
    addToCart() {
      this.tempNum += 1;
      this.$refs.hcCart.updataCart({})
    },
    toGoodsDetails() {
      this.$router.push({path: '/mdcOrder/goodsDetails'})
    }
  },
  mounted(){
    this.getProductList({storeId:'1'})
  }
}
</script>

<style lang="scss">
.hc-header {
  height: px2rem(48);
  font-size:  px2rem(16);
  .mint-header-button{
    -webkit-box-flex: .1;
    -ms-flex: .1;
    flex: .1;
  }
  .mint-header-title{
    text-align: left;
  }
  .mintui{
    font-size:  px2rem(15);
  }
}
.hc-store-list{
  .hc-store-price {
    position: relative;
    padding: 25px 0 10px;
      .hc-cart-btn {
        right: 0px;
        top: 14px;
        bottom: 0px;
        margin: auto;
      }
  }
    .hc-store-item{
       background: #fff;
       padding:  px2rem(7)  px2rem(10);
       border-bottom: 1px solid #dbdbdb;
       .hc-store-content{
         padding-left:  px2rem(10);
       }
       h3{
         font-size:  px2rem(8);
       }
       p{
          margin: 0;
          font-size: px2rem(5);
       }
       .thrume{
         width: 100%;
       }
       .price{
          font-size:  px2rem(8);
          color: red;
       }
    }
  }
.hc-nav-content {
    height:  px2rem(72);
    background-color: #f6f6f6;
    .hc-nav-item {
      width: 20%;
      height: 100%;
      float: left;
      display: flex;
      align-items: center;
      color: #776e6f;
      &.active{
        background: #fff;
      }
      .icon {
        font-size: 50px;
        margin-bottom: 10px;
        display: inline-block;
      }
      .hc-nav-items{
        width: px2rem(72);
        font-size:  px2rem(8);
        display: inline-block;
        &.hc-nav-items-bg{
          width:  px2rem(23);
        }
      }
    }
  }

  .hc-main-content {
    border-top: 1px solid #ccc;
    .hc-main-item {
      background-color: #fff;
      width: 100%;
      // box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.16);
      // border-radius: 5px;
      padding: 15px 15px 15px 100px;
      margin-bottom: 10px;
      .hc-main-sign {
        width:  px2rem(65);
        height:  px2rem(65);
        border-radius: 50%;
        background-color: #000;
        margin-right: 15px;
        left: 15px;
        top: 15px;
        background-position: center;
        background-size: cover;
        background-position: no-repeat;
      }
      .hc-main-introduce {
        line-height: 20px;
        .hc-main-star {
          padding-top: 2px;
        }
        .hc-main-label {
          margin-top: 10px;
          color: #5ad28e;
          line-height: 18px;
          .hc-main-label-1 {
            border: 1px solid #5ad28e;
            display: inline-block;
            padding-left: 24px;
            padding-right: 6px;
            position: relative;
            margin-right: 20px;
            .hc-main-label-icon {
              background-color: #5ad28e;
              display: inline-block;
              position: absolute;
              left: 0px;
              top: 0px;
              width: 18px;
              text-align: center;
              margin-right: 6px;
            }
          }
          .hc-label2-2 {
            padding-left: 6px !important;
          }
        }
      }
    }
    .hc-loading {
      margin-bottom: 10px;
    }
  }
</style>



